<template>
  <div class="section">
    <div class="columns">
      <div class="column is-offset-1 is-7">
        <div class="notification is-success is-my-6">
          <b>Chamada Gemidão é o serviço que realiza ligações para qualquer número (fixo e móvel) do Brasil e ao atender
            o aúdio mais famoso do Whatsapp é tocado.<br>
          </b>
        </div>
        <div class="content">
          <div class="fb-like" data-href="https://www.facebook.com/Chamada-Gemid%C3%A3o-1033730403430716/" data-layout="standard" data-action="like" data-size="small" data-show-faces="true" data-share="true"></div>
        </div>
        <div class="field is-horizontal">
          <div class="field-body">
            <div class="field">
              <p class="control">
                <button class="button is-info button-max button-max-desktop" @click="show.comoUsar = true">Como comprar e realizar ligações</button>
              </p>
            </div>
            <div class="field">
              <p class="control">
                <button class="button is-warning button-max button-max-desktop" @click="show.info = true">Dúvidas Gerais</button>
              </p>
            </div>
          </div>
        </div>

        <div v-if="getTermos() || auth.token">
          <div class="field is-horizontal">
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <button class="button is-success button-max button-max-desktop" @click="show.cadastro = true">Comprar ligações</button>
                </div>
              </div>
              <div class="field" v-if="auth.token">
                <div class="control">
                  <button class="button is-danger button-max button-max-desktop" @click="eventoGa('remover-autenticacao', info.numero); removerAutenticacao();">Remover Autenticação</button>
                </div>
              </div>
            </div>
          </div>
          <div v-if="info.numero">
            <br>
            <div class="notification is-primary">
              <div>
                <b>{{info.numero}} saldo de {{info.saldo}} {{info.saldo > 1 ? 'Ligações': 'Ligação'}}</b>
                <button class="button is-warning is-small" :class="{'is-loading': infoAtualizando}" @click="getInfo(); getTransacoes(); eventoGa('atualizar-saldo', info.numero + ' - ' + info.saldo);">Atualizar Saldo e Histórico</button>
              </div>

            </div>
          </div>
          <div v-if="auth.token">
            <div><br>
              <div class="field is-horizontal">
                <div class="field-body">
                  <div class="field">
                    <p class="control">
                      <input name="de" class="input"  type="text" v-mask="'(##)?#####-####'" v-model="chamada.de" placeholder="Numero com ddd do remetente (bina)">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <input name="para" class="input" type="text" v-mask="'(##)?#####-####'"v-model="chamada.para" placeholder="Numero com ddd do destinatário">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <button class="button is-success button-max" :class="{'is-loading': chamada.enviando}" :disabled="!rawNumero(chamada.de).match('^[0-9]{10,11}$') || !rawNumero(chamada.para).match('^[0-9]{10,11}$') || info.saldo <= 0" @click="ligar(); eventoGa('ligar', info.numero + ' de: '+ chamada.de + ' para: '+ chamada.para);">Ligar</button>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="transacoes && transacoes.length"><br>
            <div class="box" v-for="t of transacoes">
              <div class="media">
                <div class="media-content">
                  <div class="content has-text-centered">
                    <div class="element">De: <b>{{t.ligacao.de}}</b></div>
                    <div class="element">Para: <b>{{t.ligacao.para}}</b></div>
                    <div class="element">Status: <b>{{t.status_retorno ? t.status_retorno : 'aguarde...'}}</b></div>
                    <div class="element">Data: <b>{{dataFormat(t.cadastro)}}</b></div>
                    <div class="element"><button class="button is-success is-small" @click="chamada.de = t.ligacao.de; chamada.para = t.ligacao.para; ligar();">Religar</button></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <br>
        <div class="field">
            <div class="field-body">
              <div class="field" v-if="!getTermos() && auth.token == ''">
                <p class="control">
                  <button class="button is-warning button-max button-max-desktop" @click="exibirTermos=!exibirTermos">Cadastre-se</button>
                </p>
              </div>
              <div class="field" v-if="auth.token == ''">
                <p class="control">
                  <button class="button is-success button-max button-max-desktop" @click="show.autenticacao = true">Autenticar com número e senha</button>
                </p>
              </div>
            </div>
          </div>
      </div>
      <div class="column">
        <a class="twitter-timeline"  href="https://twitter.com/search?q=chamadagemidao.com" data-widget-id="892482850624327681">Tweets sobre chamadagemidao.com</a>
      </div>
      <div v-if="exibirTermos">
        <div class="modal is-active">
          <div class="modal-background"></div>
          <div class="modal-content">
            <div class="notification is-termo is-my-6">
              <h4><b>Termos de Uso do Chamada Gemidão</b></h4>
              <p>Termos e Condições de Compra e Venda de Chamada Gemidao, com sede na https://chamadagemidao.com, doravante denominada simplesmente Chamada Gemidao, e, de outro lado, o cliente Chamada Gemidao, qualificado no momento da compra dos produtos Chamada Gemidao, doravante denominado simplesmente Cliente.
                <br>
                Considerando que a Chamada Gemidao realiza venda de serviços pela internet; Considerando o interesse do Cliente na compra dos serviços oferecidos pelo Chamada Gemidao; O presente contrato tem por finalidade estabelecer as condições gerais de uso e compra de serviços do cliente do site Chamada Gemidao.
                <br>
                <b>I.</b> Confidencialidade: é de responsabilidade da Chamada Gemidao a preservação da confidencialidade de todos os dados e informações fornecidos pelo Cliente no processo de compra".
                <br>
                <b>II.</b> Serviço de Atendimento ao Cliente (SAC): O cliente dispõe desse serviço para sanar suas dúvidas, solucionar eventuais solicitações ou reclamações a respeito do seu pedido ou de qualquer conteúdo disponibilizado no site. O SAC poderá ser acionado por de chat no site.
                <br>
                <b>III.</b> - O Cliente deverá solicitar a devolução através do Serviço de Atendimento ao Cliente (SAC).
                <br>
                <b>IV.</b> - A compra e realizada via PagSeguro por fornecer varias formas de pagamento e segurança.
                <br>
                <b>V.</b> - Em Todas chamadas realizadas é enviado ao destinatário um SMS com o numero real do remetente.
                <br>
                <b>VI.</b> - É de total responsabilidade do cliente (remetente da mensagem) o ocorrimento de eventuais transtornos envolvendo a pessoa ou pessoas que atenderem a ligação proveniente deste serviço.
            </p>
              <div class="field is-grouped">
                <p class="control">
                  <button class="button is-success" @click="setTermos(true)">Aceitar</button>
                </p>
                <p class="control">
                  <button class="button is-success" @click="exibirTermos=!exibirTermos">Recusar</button>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="show.comoUsar">
        <div class="modal is-active" @click="closeModal()">
          <div class="modal-background"></div>
          <div class="modal-content" @click.stop>
            <div class="notification is-my-6">
              <h3 class="h3">Como cadastrar</h3>
              <ul>
                <li>É necessário comprar ligação para usar o sistema. Selecione o pacote desejado, realize o pagamento via PagSeguro, quando o pagamento for aprovado o crédito será liberado e voce receberá um SMS com a senha para o acesso.</li>
              </ul><br>
              <h3 class="h3">Autenticação</h3>
              <ul>
                <li>
                  Insira o seu número de celular com ddd e a senha recebido por SMS e clique no botão autenticar.
                </li>
              </ul><br>
              <h3 class="h3">Realizando ligações</h3>
              <ul>
                <li>Insira o número que irá aparecer na bina (exatamente, você pode alterar o numero que será exibido) e o número do destinatário, após 2 minutos que o destinatário atender ele irá receber um SMS com o número real do remetente.</li>
              </ul><br>
              <h3 class="h3">Pós ligações realizadas</h3>
              <ul>
                <li>
                  Após realizar a ligação, o extrato de ligações exibirá o status da chamada, "atendida" significa que a ligação foi atendida pelo destinatário, caso outro status seja exibido o saldo da ligação é devolvido.
                </li>
              </ul>
            </div>
          </div>
          <button class="modal-close is-large" @click="show.comoUsar = false"></button>
        </div>
      </div>
      <div v-if="show.cadastro">
        <div class="modal is-active" @click="closeModal()">
          <div class="modal-background"></div>
          <div class="modal-content" @click.stop>
            <div class="notification">
              <div >
                <div >
                  <div class="field">
                    <p class="control">
                      <input type="text" v-mask="'(##)?#####-####'" placeholder="Meu celular com ddd" v-model="meuNumero" class="input">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <input type="text" v-mask="'(##)?#####-####'" placeholder="Repetir número" v-model="meuNumero2" class="input">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control has-icons-right">
                  <span class="select">
                    <select v-model.number="planoEscolhido" >
                      <option v-for="(p, i) of planos" :value="i">{{i}} {{i == 1 ? 'Ligação': 'Ligações'}} - R${{p}}</option>
                    </select>
                  </span>
                    </p>
                  </div>
                  <div class="field">
                    <div class="control">
                      <button class="button is-success button-max" name="numeroLogin" :disabled="!rawNumero(meuNumero).match('^[0-9]{10,11}$') || meuNumero != meuNumero2" :class="{'is-loading': comprando}" @click="comprando = true; comprar(); eventoGa('compra', 'plano '+ planoEscolhido)">Comprar</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <button class="modal-close is-large" @click="closeModal()"></button>
        </div>
      </div>
      <div v-if="show.autenticacao">
        <div class="modal is-active" @click="closeModal()">
          <div class="modal-background"></div>
          <div class="modal-content" @click.stop>
            <div class="notification">
              <div class="">
                <div class="field">
                  <p class="control">
                    <input class="input" type="text" v-mask="'(##)?#####-####'" placeholder="Meu numero com ddd" v-model="auth.numero">
                  </p>
                </div>
                <div class="field">
                  <p class="control">
                    <input class="input" type="password" placeholder="Senha" v-model="auth.senha">
                  </p>
                </div>
                <div class="field">
                  <p class="control">
                    <button class="button is-success button-max" :disabled="!rawNumero(auth.numero).match('^[0-9]{10,11}$') || auth.senha == ''" :class="{'is-loading': auth.autenticando}" @click="autenticar(); eventoGa('autenticacao', auth.numero);">Autenticar</button>
                  </p>
                </div>
              </div>
            </div>
          </div>
          <button class="modal-close is-large" @click="closeModal()"></button>
        </div>
      </div>
      <div v-if="show.info">
        <div class="modal is-active" @click="closeModal()">
          <div class="modal-background"></div>
          <div class="modal-content" @click.stop>
            <div class="notification is-danger is-my-6">
              <b>Atenção, ligações que cair na caixa postal serão cobradas e ficarão com status "atendida", se ainda houver dúvidas pode entrar em contato com o suporte.
              </b>
            </div>
            <div class="notification is-my-6 is-termo">
              <dl>
                <dd>- Sempre o destinatário recebe o SMS com o número do remetente original, exceto quando o numero do destinatário é fixo</dd><br>
                <dd>- As compras são processadas pelo PagSeguro e quando a compra fica com o status aprovado o crédito é liberado</dd><br>
                <dd>- Compra por boleto: O crédito é liberado após a aprovação do pagamento junto com a senha</dd><br>
                <dd>- Caso necessite gerar uma nova senha entre em contato com o suporte</dd><br>
                <dd>- Sempre insirá os números com ddd</dd><br>
              </dl>
            </div>
          </div>
          <button class="modal-close is-large" @click="closeModal()"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '@/services/http';
  import toast from '@/services/toast'
  import moment from 'moment'

  export default {
    name: 'chamadaDoGemidao',
    data () {
      return {
        show: {
          comoUsar: false,
          cadastro: false,
          autenticacao: false,
          info: false,
        },
        planoEscolhido: 1,
        planos: {
          1: '1,19', 2: '2,00', 3: '2,75', 4: '3,50', 5: '4,25',
          6: '5,00', 7: '5,75', 8: '6,50', 9: '7,25', 10: '8,00',
          15: '11,75', 20: '15,50', 30: '23,00', 40: '30,50', 50: '38,00'
        },
        meuNumero: '',
        meuNumero2: '',
        comprando: false,
        auth: {
          numero: '',
          senha: '',
          autenticando: false,
          exibir: false,
          token: '',
        },
        transacoes: [],
        info: {},
        infoAtualizando: false,
        chamada: {
          de: '',
          para: '',
          enviando: false
        },
        exibirTermos: false,
      }
    },

    methods: {
      dataFormat: function (d) {
        return moment.unix(d).format('DD/MM/YY HH:mm:ss')
      },
      removerAutenticacao: function () {
        window.localStorage.removeItem('token');
        this.auth.token='';
        this.info = {};
        this.transacoes = [];
      },
      pagseguro: function (code) {
        PagSeguroLightbox({
          code: code,

        }, {
          success : transactionCode => {
            http.get('/transacao?transacao_id=' + transactionCode)
          },
          abort : function() {
            console.log('abortado')
          }
        })
      },

      comprar: function () {
          http.post('/comprar', {numero: this.rawNumero(this.meuNumero), plano: this.planoEscolhido}).then(r => {
              if (r.data.code) {
                  this.pagseguro(r.data.code)
              }
              this.comprando = false;
          })
      },

      autenticar: function () {
          this.auth.autenticando = true;
          http.post('/login', {username: this.rawNumero(this.auth.numero), password: this.auth.senha}).then(r => {
            if (r.data.token) {
                localStorage.setItem("token", r.data.token)
                localStorage.setItem("tokenExpire", r.data.expire)
                window.location.reload();
            }
            return;
          }).catch(err => {
              toast.error('Numero ou senha incorreto');
              return;
          }).then(r => {
            this.auth.autenticando = false;
          })
      },

      getTransacoes: function () {
          http.get('/auth/chamadas/historico').then(r => {
              this.transacoes = r.data
          }).catch(err => {
              // token sem permissao
          })
      },

      getInfo: function () {
          this.infoAtualizando = true
          http.get('/auth/info').then(r => {
              this.info = r.data;
              return;
          }).catch(err => {
              toast.error('Não foi possivel obter as informações');
              return;
          }).then(() => {
            this.infoAtualizando = false;
        });

      },

      ligar: function () {
          this.chamada.enviando = true;
          http.post('/auth/chamada', {de: this.rawNumero(this.chamada.de), para: this.rawNumero(this.chamada.para)}).then(r => {
              if (r.data.status == 'ok') {
                  toast.success(r.data.message);
                  if (this.info.saldo > 0) {
                      this.info.saldo = this.info.saldo - 1;
                  }
                  return;
              }
              toast.error(r.data.message)
              return;
          }).catch(() => {
              toast.success('Algum problema com a rede ou o serviço, aguarde alguns instantes e tente novamente')
              return;
          }).then(() => {this.chamada.enviando = false;})
      },

      eventoGa: function (categoria, acao) {
        ga('send','event', categoria, acao);
      },

      getTermos: function () {
        return window.localStorage.getItem('termos')
      },
      setTermos: function (status) {
        window.localStorage.setItem('termos', status);
        window.location.reload();
      },

      closeModal: function () {
        this.show.comoUsar = false;
        this.show.cadastro = false;
        this.show.autenticacao = false;
        this.show.info = false;
      },

      rawNumero: function (v) {
        return v.replace(/\D/g, '')
      }
    },

    created: function () {
      let token = localStorage.getItem('token');
      if (token) {
        this.auth.token = token;
        this.getTransacoes();
        this.getInfo();
      }
      document.addEventListener('keydown', (e) => {
        if (e.keyCode === 27) {
          this.closeModal();
        }
      })
    }
  }
</script>

<style scoped>
.section {
  padding: 1rem 1.5rem;
}

.element {
  margin-left: 1rem;
  font-size: .9rem;
  /*font-weight: bold;*/
  display: inline-block;
  max-width: 10rem;
  min-width: 2rem;
  text-align: center;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.is-termo {
  text-align: left;
}

.button-max-desktop {
  width: 100%;
}

.h3 {
  font-weight: bold;
  font-size: 17px;
}

@media screen and (max-width: 768px) {
  .element {
    font-size: .75rem;
    max-width: 5rem;
  }

  .is-my-6 {
    font-size: .8rem;
  }

  .button-max {
    width: 100%;
  }
}
</style>



